.equipments {
  margin-bottom: var(--margin-big-desktop);

  &__information {
    display: flex;
    justify-content: center;
    margin-bottom: 75px;
  }

  &__title {
    max-width: 1000px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 13px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    padding: 112px 40px 16px 40px;
    max-width: 315px;
    background-position: 40px 16px;
    background-repeat: no-repeat;
    background-color: var(--main-bg);

    &--technologies {
      display: flex;
      align-items: center;
      justify-content: center;
      grid-column: 3 / 5;
      padding: 0;
      min-height: 162px;
      max-width: 643px;

      .equipments__text {
        margin-bottom: 0;
      }
    }

    &--sea-container {
      background-image: url("../img/equipments/sea-container.svg");
    }

    &--pumps {
      background-image: url("../img/equipments/pumps.svg");
    }

    &--electrics-lights {
      background-image: url("../img/equipments/electrics-lights.svg");
    }

    &--oil {
      background-image: url("../img/equipments/oil.svg");
    }

    &--gas-jet {
      background-image: url("../img/equipments/gas-jet.svg");
    }

    &--meteo {
      background-image: url("../img/equipments/meteo.svg");
    }

    &--chemistry {
      background-image: url("../img/equipments/chemistry.svg");
    }

    &--block-module {
      background-image: url("../img/equipments/block-module.svg");
    }

    &--o2 {
      background-image: url("../img/equipments/o2.svg");
    }

    &--boiler {
      background-image: url("../img/equipments/boiler.svg");
    }

    &--round {
      background-image: url("../img/equipments/round.svg");
    }
  }

  &__text {
    display: block;
    flex-grow: 0;
    margin: 0;
    margin-bottom: auto;
    min-width: 255px;
    font-weight: 400;
    font-size: 15px;
  }
}