.accost {
  margin-bottom: var(--margin-big-desktop);

  &__container {
    display: flex;
    align-items: center;
    padding: 30px 35px 20px 50px;
    background-color: var(--main-bg);
  }

  &__information {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-right: 50px;
  }

  &__testimonials {
    max-width: 586px;
  }

  &__title {
    margin: 0;
    padding-bottom: 30px;
    max-width: 580px;
  }

  &__text {
    margin: 0;
    max-width: 470px;
    font-weight: 400;
    font-size: 14px;
    color: var(--features-text);
  }

  &__contacts-manager {
    padding-bottom: 30px;
  }

  &__link {
    font-weight: 400;
    font-size: 18px;
    color: var(---second-text-color);
  }
}

.contacts-manager {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 90px;
  max-width: 220px;

  &__icon {
    display: inline-block;
    font-size: 0;
  }

  &__link {
    padding-left: 8px;
    font-weight: 400;
    font-size: 18px;
    color: var(--second-text-color);
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    padding-bottom: 9px;
    width: 220px;
  }
}

.whatsapp-icon {
  padding-right: 6px;
}