.nav {
  &__list {
    display: flex;

    @include tablet {
      display: block;
      background-color: transparent;
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 20px;
    }

    &--footer {
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }

    @include tablet {
      position: relative;
      max-width: 45%;

      &:not(:last-child) {
        line-height: 35px;
      }

      &:last-child {
        margin-top: 40px;
      }
    }

    @include mini-mobile {
      max-width: 100%;
      text-align: center;

      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }

  &__btn {
    position: relative;
  }

  &__arrow_submenu {
    margin-bottom: 0;

    &::before {
      content: "";
      position: absolute;
      left: 63%;
      top: 17%;
      width: 20px;
      height: 20px;
      background-image: url("../img/arrow_submenu.svg");
      background-position: center;
      background-size: 38%;
      background-repeat: no-repeat;
      background-color: var(--black);
      visibility: hidden;
      transition: transform 0.7s ease-in-out;
      cursor: pointer;
    }

    &:focus {
      &::before {
        transform: rotate(90deg);
      }
    }

    &:hover {
      &::before {
        transform: rotate(90deg);
      }
    }

    &:active {
      &::before {
        transform: rotate(90deg);
      }
    }

    @include tablet {
      padding-bottom: 0;

      &::before {
        visibility: visible;
      }
    }
  }

  &__link {
    position: relative;
    display: block;
    font-weight: 400;
    font-size: 14px;
    color: var(--second-text-color);
    transition: color 0.3s ease-in-out;

    &--footer {
      position: relative;
      font-weight: 500;
      font-size: 16px;
      color: var(--white-text);

      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: #fff;
        transform: scale(0);
        transition: 0.3s ease-in-out;
      }

      &:hover {
        &::after {
          transform: scale(1);
        }
      }
    }

    @include min-width-tablet {
      &:hover {
        color: var(--main-color);
      }
    }

    @include tablet {
      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        z-index: -1;
        width: 0%;
        height: 100%;
        background-color: #000;
        transition: width 0.3s ease-in-out;
      }

      &:hover {
        color: var(--white-text);

        &::after {
          width: 100%;
          color: var(--white-text);
        }
      }
    }

    @include tablet {
      font-size: 24px;
    }

    @include mini-mobile {
      font-weight: 700;
      font-size: 14px;
      text-transform: uppercase;
      color: #777;
    }
  }
}