.about-option {
  margin-bottom: var(--margin-big-desktop);
  padding: 40px 0;
  background-color: var(--main-bg);

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    font-weight: 700;
    font-size: 30px;
  }

  &__about {
    display: flex;
    flex-direction: column;
    padding: 36px 47px;
    width: 49%;
    background-color: var(--box-bg);
  }

  &__descr {
    width: 49%;
  }

  &__list {
    display: flex;
    flex-direction: column;
  }

  &__bold-text {
    font-weight: 700;
    font-size: 15px;
    color: var(--main-text-color);
  }

  &__descr-text {
    padding-right: 100px;
    font-weight: 400;
    font-size: 15px;
    color: var(--main-text-color);
  }

  &__text {
    position: relative;
    padding-left: 57px;
    font-weight: 400;
    font-size: 15px;
    color: var(--main-text-color);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 30px;
      height: 30px;
      background-image: url("../img/featuresItem.svg");
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}