.setting-up {
  margin-bottom: var(--margin-big-desktop);

  &__gallery {
    display: flex;
  }

  &__title {
    padding-right: 112px;
    width: 55%;
  }

  &__text {
    margin: 0;
    width: 45%;
  }

  &__header {
    display: flex;
    margin-bottom: 53px;
  }

  &__item {
    &:not(:last-child) {
      padding-right: 25px;
    }
  }
}
