.card {
  display: flex;
  flex-direction: column;
  padding: 17px 15px;
  height: 100%;
  background-color: var(--main-bg);

  &__button {
    margin-top: auto;
    border: 1px solid var(--main-color);
    border-radius: 26px;
    padding: 18px 0;
    min-width: 285px;
    font-weight: 400;
    font-size: 15px;
    text-align: center;
    color: var(--main-color);
    background-color: var(--main-bg);
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
      color: var(--white-text);
      background-color: var(--main-color);
      transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
    }
  }

  &__name {
    margin: 0;
    padding-bottom: 10px;
    font-weight: 700;
    font-size: 15px;
  }

  &__title {
    margin: 0;
    padding-bottom: 30px;
    font-weight: 700;
    font-size: 15px;
  }

  &__about {
    overflow: hidden;
    display: -webkit-box;
    margin: 0;
    margin-bottom: 20px;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
  }

  &__image {
    padding-bottom: 20px;
  }

  &__date {
    margin: 0;
    padding-bottom: 10px;
    font-weight: 500;
    font-size: 15px;
    color: var(--main-color);
  }
}