.footer {
  background-image: url("../img/footer/footer-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__nav {
    font-weight: 500;
    font-size: 16px;
  }

  &__title {
    margin: 0;
    padding-bottom: 10px;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    padding-bottom: 50px;
  }

  &__phone-link {
    font-weight: 700;
    font-size: 18px;
    color: var(--white-text);
  }

  &__office {
    display: flex;
    flex-direction: column;
  }

  &__office-address {
    margin: 0;
    max-width: 270px;
    font-weight: 500;
    font-size: 16px;
    color: var(--white-text);
  }

  &__email-link {
    font-weight: 400;
    font-size: 14px;
    color: var(--white-text);
  }

  &__numbers-phone {
    font-weight: 500;
    font-size: 16px;
    color: var(--white-text);
  }

  &__numbers-item {
    padding-bottom: 50px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    padding-top: 51px;
    padding-bottom: 30px;
  }

  &__logo {
    padding-bottom: 40px;
  }

  &__logo-img {
    max-width: 113px;
    max-height: 36px;
    object-fit: contain;
  }

  &__company {
    padding-bottom: 12px;
    font-weight: 500;
    font-size: 16px;
    color: var(--white-text);
  }

  &__sites {
    padding-bottom: 30px;
  }
}

.links {
  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 187%;
    text-decoration: underline;
    color: var(--white-text);
  }
}

.socials {
  &__list {
    display: flex;
    justify-content: flex-start;
  }

  &__item {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: var(--black);

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__link {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;

    svg {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);

      path {
        transition: fill 0.3s ease-in-out;
      }
    }

    &-youtube {
      fill: none;

      &:hover {
        svg path {
          fill: #f00;
        }
      }
    }

    &-inst {
      fill: none;

      &:hover {
        svg path {
          fill: rgb(76, 56, 100);
        }
      }
    }

    &-facebook {
      fill: none;

      &:hover {
        svg path {
          fill: rgb(47, 113, 212);
        }
      }
    }
  }
}