.header--news {
  &__container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin: 0 auto;
    margin-top: 20px;
    padding: 0 var(--container-offset);
    max-width: var(--content-width-three);

    .burger {
      @include mobile {
        width: 31px;
        height: 25px;
      }
    }
  }

  &__nav {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 10px;
    width: 100%;
    background-color: transparent;

    .nav__list {
      justify-content: flex-start;
      border: none;
      padding: 0;
      padding-left: calc((100% - 1170px)/2);
      width: 100%;

      @include tablet {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        height: 100%;
      }
    }
  }
}

.breadcrumb {
  @include tablet {
    justify-content: flex-start;
  }
}