.hero-contacts {
  margin-bottom: var(--margin-big-desktop);

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__information {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 39px;
    padding-right: 88px;
    background-image: url("../img/hero/text-bg.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-color: rgb(0 0 0 / 40%);
    }
  }

  &__breadcrumb {
    padding-bottom: 14px;
  }

  &__leaders {
    margin-left: 13px;
  }

  &__title {
    position: relative;
    z-index: 3;
    margin: 0;
    padding-bottom: 144px;
    width: 385px;
    color: var(--white-text);

    &_mini {
      margin: 0;
      margin-bottom: 10px;
      font-weight: 700;
      font-size: 15px;
      color: var(--white-text);
    }
  }

  &__index {
    padding-bottom: 30px;
  }

  &__address {
    z-index: 3;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__phones {
    display: flex;
    flex-direction: column;
  }

  &__text {
    z-index: 2;
    margin: 0;
    max-width: 380px;
    font-weight: 400;
    font-size: 15px;
    color: var(--white-text);
  }
}
