.auto-system-control-features {
  margin-bottom: var(--margin-big-desktop);
  background-color: var(--main-bg);

  &__container {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  &__about {
    display: flex;
    padding-bottom: 50px;
  }

  &__tag {
    text-align: center;
  }

  &__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding-bottom: 50px;
    width: 98%;
    max-height: 350px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    color: var(--second-text-color);
  }

  &__item {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 35px;
    padding-left: 60px;
    width: 32%;
    height: 36px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 50%;
      width: 40px;
      height: 40px;
      background-image: url("../img/white-plus.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-color: var(--main-color);
      transform: translateY(-50%);
    }
  }
}