.tasks {
  margin-bottom: var(--margin-big-desktop);
  background-color: var(--main-bg);

  &__container {
    display: flex;
    justify-content: space-between;
    padding-top: 35px;
    padding-bottom: 35px;
  }

  &__information {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 19px;
    padding-right: 80px;
  }

  &__title {
    margin: 0;
    max-width: 345px;
  }

  &__text {
    max-width: 470px;
    font-weight: 400;
    font-size: 15px;
    color: var(--black);
  }
}

.exercise {
  padding: 35px 15px 35px 35px;
  max-width: 750px;
  background-color: var(--box-bg);

  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    padding-top: 50px;
    max-width: 165px;
    text-align: center;
    background-position: top center;
    background-repeat: no-repeat;

    &--defrosting {
      background-image: url("../img/defrosting.svg");
      background-size: 25px 43px;
    }

    &--completing {
      background-image: url("../img/completing.svg");
      background-size: 49px 43px;
    }

    &--heatingmanufacture {
      background-image: url("../img/heatingmanufacture.svg");
      background-size: 43 px 43px;
    }

    &--refill {
      background-image: url("../img/refill.svg");
      background-size: 45px 43px;
    }

    &--heating {
      background-image: url("../img/heating.svg");
      background-size: 43px 43px;
    }

    &--hottingjbi {
      background-image: url("../img/hottingjbi.svg");
      background-size: 43px 24px;
    }

    &--hotwater {
      background-image: url("../img/hotwater.svg");
      background-size: 28px 43px;
    }
  }

  &__about {
    font-weight: 400;
    font-size: 15px;
    text-align: center;
  }
}