.information {
  margin-bottom: var(--margin-big-desktop);

  &__container {
    padding: 0 220px;
  }

  &__title {
    padding: 0 20px;
    padding-bottom: 5px;
    text-align: center;

    &_bottom {
      padding-bottom: 30px;
    }
  }

  &__title span {
    color: var(--main-color);
  }

  &__text {
    margin: 0;
    padding-top: 6px;
    padding-bottom: 12px;
    font-weight: 400;
    font-size: 15px;
    text-align: center;
    color: var(--cards-info-text);

    &_short {
      padding: 0 130px;
      padding-bottom: 6px;
    }

    &_middle {
      padding: 0 40px;
      padding-bottom: 12px;
    }
  }
}

.cities {
  display: flex;
  justify-content: center;
  margin: 0;

  &__item {
    position: relative;
    font-weight: 600;
    font-size: 15px;
    line-height: 133%;
    text-align: center;
    color: var(--cards-info-text);

    &:not(:last-child) {
      padding-right: 22px;

      &::after {
        content: "";
        position: absolute;
        right: 7px;
        top: 0;
      }
    }
  }
}

.information-auto-system-control {
  &__container {
    display: flex;
    padding: 0;
    justify-content: flex-start;
  }

  &__text,
  &__title {
    text-align: left;
    width: 50%;
  }
}