.services {
  margin-bottom: var(--margin-big-desktop);

  &__title {
    display: inline;
    margin: 0;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 196px);
    gap: 16px 30px;
  }

  &__text {
    margin: 0;
    max-width: 755px;
    font-weight: 400;
    font-size: 16px;
  }

  &__item {
    position: relative;
    grid-column: 1 span;
    grid-row: 1 span;
    padding: 131px 160px 17px 27px;
    max-width: 415px;
    text-align: center;
    transition: 0.3s ease-in-out;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 2;
      background: linear-gradient(to top, #000 1%, transparent 100%);
      transition: 0.3s ease-in-out;
    }

    &:hover {
      &::after {
        background: none;
        animation-name: bg-gradient;
        animation-duration: 0.1s;
        animation-fill-mode: forwards;
      }
    }

    &--steamgenerator {
      background-image: url("../img/services/airgenerator.jpg");
    }

    &--recycler {
      background-image: url("../img/services/prodrecycling.jpg");
    }

    &--choisegenerator {
      background-image: url("../img/services/checkairgenerator.jpg");
    }

    &--ASU {
      background-image: url("../img/services/ASU.jpg");
    }

    &--montage {
      background-image: url("../img/services/montage.jpg");
    }

    &--modulemontage {
      background-image: url("../img/services/modulemontage.jpg");
    }

    &--filterpress {
      background-image: url("../img/services/filterpress.jpg");
    }
  }

  .arrow-link {
    content: "";
    position: absolute;
    right: 20px;
    top: 16px;
    z-index: 3;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-image: url("../img/arrow-link.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--black);
    transform-origin: center;
    transition: 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
      transform: scale(1.2);
    }
  }

  &__name {
    position: relative;
    z-index: 3;
    margin: 0;
    font-weight: 700;
    font-size: 20px;
    text-align: left;
    color: var(--white-text);
  }
}

@keyframes bg-gradient {
  0% {
    background: linear-gradient(to top, #000 0%, transparent 100%);
  }

  50% {
    background: linear-gradient(to top, #000 1%, transparent 70%);
  }

  100% {
    background: linear-gradient(to top, #000 0%, transparent 0%);
  }
}
