.dropdown {
  position: absolute;
  z-index: 4;
  display: none;
  min-width: 550px;
  background-color: var(--main-bg);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &__list {
    padding-left: 5px;

    // background-color: var(--main-bg);

    @include tablet {
      padding-left: 0;
    }
  }

  &__link {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    color: var(--second-text-color);
    background-color: transparent;
    transition: color 0.3s ease-in-out;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      left: -5px;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: -1;
      width: 0%;
      height: 100%;
      background-color: var(--black);
      transition: width 0.3s ease-in-out;

      @include tablet {
        left: 0;
        width: 0%;
        height: 100%;
      }
    }

    @include tablet {
      display: inline;
      text-align: center;
    }
  }

  &__item {
    position: relative;
    background-color: transparent;

    &:hover {
      .dropdown__link {
        z-index: 9;
        color: var(--white-text);

        &::after {
          width: 101%;
          color: var(--white-text);
        }
      }
    }
  }

  &--active {
    display: block;
    opacity: 1;
    visibility: visible;
  }

  @include tablet {
    position: relative;
    min-width: 100%;
    max-width: 100%;
  }
}