.news-content {
  margin-bottom: var(--margin-big-desktop);

  &__data {
    display: flex;
  }

  &__image {
    margin-bottom: 20px;
  }

  &__text {
    border-bottom: 1px solid #e7e7e7;
    padding-bottom: 50px;
  }

  &__item {
    position: relative;
    color: var(--main-color);

    &:not(:last-child) {
      padding-right: 8px;
      margin-right: 10px;

      &::after {
        position: absolute;
        content: '|';
        right: -2px;
      }
    }
  }

  &__pagination {
    display: flex;
    justify-content: center;
  }

  &__button {
    margin-right: 5px;
    border: none;
    border-radius: 100%;
    height: 26px;
    min-width: 26px;
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    background-color: transparent;
    cursor: pointer;
    transition: color 0.3s ease-in-out;

    &_disabled {
      opacity: 0.7;
      cursor: default;
    }

    &_active {
      color: var(--white-text);
      background-color: var(--main-color);
    }

    &:hover {
      color: var(--main-color);
      transition: color 0.3s ease-in-out;
    }

    &:hover.number {
      color: var(--white-text);
      background-color: var(--main-color);
    }
  }
}