.questions {
  &__question {
    border-bottom: 1px solid var(--main-border);
    padding: 26px 38px;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: var(--main-text-color);
  }

  &__item {
    padding-bottom: 10px;
  }

  &__title {
    padding-bottom: 30px;
  }

  &__text {
    padding: 26px 38px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--main-text-color);
  }

  &__header {
    padding-bottom: 40px;
  }
}

.accordion {
  &__control {
    position: relative;
    border: none;
    padding: 0;
    padding-left: 30px;
    width: 100%;
    background-color: var(--main-bg);
    cursor: pointer;
  }

  &__question {
    margin: 0;
    padding: 26px 38px 26px 0px;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    text-align: left;
    color: var(--main-text-color);
    transition: 0.3s color ease-in-out;

    &:hover {
      color: var(--main-color);
      transition: 0.3s color ease-in-out;
    }
  }

  &__arrow {
    content: "";
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 0;
    top: 0;
    width: 30px;
    height: 30px;
    background-color: var(--arrow-bg);
    transition: 0.3s transform ease-in-out, 0.3s background-color ease-in-out;
  }

  &__content {
    overflow: hidden;
    margin: 0;
    padding: 0px 38px 0px 0;
    box-sizing: content-box;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    width: 100%;
    max-height: 0px;
    line-height: 150%;
    color: var(--main-text-color);
    opacity: 0;
    transition: all 0.3s ease-in-out;
    will-change: max-height;
  }

  .open &__content {
    padding: 26px 38px 26px 0;
    color: var(--main-text-color);
    opacity: 1;
    transition: 0.3s all ease-in-out;
  }

  .open &__question {
    border-bottom: 1px solid var(--main-border);
  }

  .open &__arrow {
    background-color: var(--main-color);
    transform: rotate(180deg);
    transition: 0.3s transform ease-in-out, 0.3s background-color ease-in-out;
  }

  .open &__question {
    color: var(--main-color);
    transition: 0.3s color ease-in-out;
  }
}