.information-gallery {
  background-color: var(--main-bg);

  &__container {
    padding-top: 46px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 246px;
    gap: 60px 20px;
  }

  &__item {
    padding-bottom: 65px;
    width: 415px;
  }

  &__img {
    padding-bottom: 15px;
  }

  &__text {
    margin: 0;
    padding-right: 30px;
    font-weight: 400;
    font-size: 15px;
  }
}