.header {
  width: 100%;

  &__container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: var(--margin-top-desk);
    margin-right: 0;
    margin-bottom: var(--margin-bottom-desk);
    margin-left: 0;
    padding: 0 39px 0 42px;
    background-color: var(--main-bg);

    @include mobile {
      padding: 0 5px 0 6px;
    }
  }

  &__phone-link {
    font-weight: 700;
    font-size: 18px;
    color: var(--second-text-color);

    @include mini-mobile {
      font-size: 14px;
    }
  }

  &__email-link {
    font-weight: 400;
    font-size: 14px;
    text-align: right;
    color: var(--second-text-color);
  }

  &__contacts {
    display: flex;
    flex-direction: column;
  }

  &__logo {
    padding-top: 16px;
    padding-bottom: 25px;

    @include mini-mobile {
      padding-top: 10px;
      padding-bottom: 10px;
      max-width: 112px;
    }
  }
}

.burger {
  position: relative;
  z-index: 101;
  display: none;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;

  &::after,
  &::before {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: var(--black);
    transition: transform 0.6s ease-in-out, top 0.3s ease-in-out, bottom 0.3s ease-in-out;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - var(--burger-line-height));
  }

  &__line {
    position: absolute;
    right: 0;
    top: 50%;
    width: 70%;
    height: var(--burger-line-height);
    background-color: var(--black);
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
  }

  &--active {
    &::before {
      top: 50%;
      transform: rotate(-45deg);
    }

    &::after {
      top: 50%;
      bottom: auto;
      transform: rotate(225deg);
    }

    .burger__line {
      opacity: 0;
    }
  }
}

@include tablet {
  .burger {
    display: block;
    margin: 1px 0 0;
  }
}

@include mobile {
  .burger {
    align-self: center;
    margin: 0;
  }
}

@include tablet {
  .header__nav {
    &.nav {
      position: fixed;
      right: 0;
      top: 0;
      z-index: 101;
      overflow-y: auto;
      padding: 30px;
      width: 100%;
      height: 100vh;
      background-color: rgba(255 255 255 / 95%);
      transform: translateX(100%);
      transition: transform 0.7s ease-in-out;

      &--active {
        transform: none;
        transition: transform 0.7s ease-in-out;
      }

      @include mobile {
        max-width: 100%;
      }

      @include mini-mobile {
        padding: 84px 10px 14px 10px;
        background-color: rgba(255 255 255);
      }
    }
  }
}