.hero {
  margin-bottom: var(--margin-big-desktop);

  &__container {
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
  }

  &__information {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-right: 28px;
    padding-top: 27px;
    padding-right: 97px;
    padding-left: 39px;
    padding-bottom: 25px;
    background-image: url("../img/hero/text-bg.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-color: rgb(0 0 0 / 40%);
    }

    &_long {
      padding-right: 20px;
    }

    &_long2 {
      padding-right: 18px;
    }

    &_short {
      padding-right: 218px;
    }
  }

  &__title {
    position: relative;
    z-index: 2;
    max-width: 250px;
    color: var(--white-text);

    &_long {
      max-width: 320px;
    }

    &_long2 {
      max-width: 360px;
    }
  }

  &__gallery-list {
    display: flex;
    justify-content: space-between;
  }

  &__text {
    position: relative;
    z-index: 2;
    margin-top: auto;
    max-width: 380px;
    font-weight: 400;
    font-size: 15px;
    color: var(--white-text);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: -21px;
      width: 57px;
      height: 2px;
      background-color: var(--white-text);
    }

    &_long {
      max-width: 450px;
    }

    &_long2 {
      max-width: 460px;
    }
  }

  &__image,
  &__video {
    width: auto;
  }
}