.btn {
  border-radius: 5px;
  padding: 10px 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;

  &--fill {
    background-color: var(--background-cool);
    transition: background-color 0.3s;

    &:hover {
      background-color: var(--ligth-black);
      transition: background-color 0.3s;
    }
  }
}
