.montage-steps {
  margin-bottom: var(--margin-big-desktop);

  &__text {
    padding-top: 60px;
    padding-right: 10px;
  }

  &__about {
    display: flex;
    margin-bottom: 40px;
  }

  &__list {
    counter-reset: steps stepsWithout 9;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 45px 60px;
    background-color: var(--main-bg);
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 295px;
    height: 156px;

    &_withzero {
      &::before {
        counter-increment: steps;
        content: "0"counter(steps);
        position: absolute;
        top: 10%;
        border-radius: 50%;
        padding-top: 4%;
        width: 42px;
        height: 42px;
        font-weight: 700;
        font-size: 16px;
        text-align: center;
        color: var(--white-text);
        background-color: var(--main-color);
      }
    }

    &_withoutzero {
      &::before {
        counter-increment: stepsWithout;
        content: ""counter(stepsWithout);
        position: absolute;
        top: 10%;
        border-radius: 50%;
        padding-top: 4%;
        width: 42px;
        height: 42px;
        font-weight: 700;
        font-size: 16px;
        text-align: center;
        color: var(--white-text);
        background-color: var(--main-color);
      }
    }

    &:not(:last-child) {
      padding-right: 0;
    }
  }
}