.pagination {
  &__list {
    display: flex;
    align-items: center;
    justify-content: center;

    &_hidden {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__button {
    margin-right: 5px;
    border: none;
    border-radius: 100%;
    height: 26px;
    min-width: 26px;
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    background-color: transparent;
    cursor: pointer;

    &_disabled {
      opacity: 0.7;
      cursor: default;
    }

    &_active {
      color: var(--white-text);
      background-color: var(--main-color);
    }

    &:hover.number {
      color: var(--white-text);
      background-color: var(--main-color);
    }
  }
}