html {
  overflow-x: hidden;
  margin-right: calc(-1 * (100vw - 100%));
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  height: 100%;
  min-width: 320px;
  min-height: 100%;
  font-size: 15px;
  color: var(--main-text-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden; //     position: sticky -   
  min-height: 100%;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.footer-down {
  flex: 1 1 auto;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

@keyframes move-reverse {
  0% {
    transform: translateX(0);
    transition: all 0.2s ease;
  }

  100% {
    transform: translateX(3px);
    transition: all 0.2s ease;
  }
}

@keyframes move-fontsize {
  0% {
    transform: scale(1);
    transition: all 0.2s ease;
  }

  100% {
    transform: scale(1.2);
    transition: all 1.2s ease;
  }
}

/*settings for fontawesome*/
.check-mark::before,
.map-icon::before,
.tel-icon::before,
.tel-icon-header::before,
.email-icon::before,
.email-icon-header::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}

.main-title {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 30px;
  color: var(--black);
}

.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0 0 0 / 50%);
  opacity: 0;
  visibility: hidden;
  transition: background-color 0.3s;

  &--active {
    opacity: 1;
    visibility: visible;
  }
}

.shadow-main {
  -webkit-box-shadow: 25px 27px 45px 0px rgba(34, 60, 80, 0.12);
  -moz-box-shadow: 25px 27px 45px 0px rgba(34, 60, 80, 0.12);
  box-shadow: 25px 27px 45px 0px rgba(34, 60, 80, 0.12);
}

.text-main-shadow {
  text-shadow: #d3d3d2 1px 3px 2px;
}

.overlay-2 {
  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    background-color: rgba(0 0 0 / 35%);
    transition: background-color 0.3s;
  }
}

.copyright {
  @include flex-all-center;

  padding: 18px 22px;
  color: var(--white-text);
  background-color: var(--black);
}

.border-line {
  border-bottom: 1px solid var(--grey-line);
}

.text-white {
  color: var(--white-text);
}

.white-tag {
  padding: 14px 30px;
  height: 100%;
  color: var(--second-text-color);
  background-color: var(--tag-bg);

  @include tablet {
    padding: 10px;
  }
}

.green-tag {
  padding: 30px 33px;
  height: 100%;
  color: var(--white-text);
  background-color: var(--green-tag-bg);
}

.news-tag {
  padding: 11px 22px;
  color: var(--white-text);
  background-color: var(--green-tag-bg);
}

.white-tag,
.green-tag,
.news-tag {
  margin: 0;
  border-radius: 100px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
}

.scrollToTop {
  position: fixed;
  right: 70px;
  bottom: 50px;
  z-index: 100;
  width: 50px;
  height: 50px;
  font-size: 50px;
  opacity: 0.75;
  transition: opacity 0.3s;
  cursor: pointer;

  svg {
    fill: var(--main-color);
    transition: fill 0.3s ease-in-out;
  }

  &:hover {
    svg {
      fill: var(--main-color-active);
    }
  }

  &.isShowBtn {
    &_hide {
      opacity: 0;
      transition: opacity 0.3s;
    }
  }
}