.news-gallery {
  &__breadcrumbs {
    margin-top: 25px;
  }

  &__title {
    color: var(--second-text-color);
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 14px;
  }

  &__list-news {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 14px;
  }

  &__cards {
    padding-bottom: 71px;
  }

  &__pagination {
    display: flex;
    justify-content: center;
    padding-bottom: 187px;
  }
}