//     sass- -  root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --second-family: "OpenSans", sans-serif;
  --third-family: "Inter", sans-serif;

  // --third-family: "Inter", sans-serif;
  --content-width: 1300px;
  --container-offset: 10px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --margin-big-desktop: 100px;
  --margin-bottom-desk: 15px;
  --margin-tablet: 30px;
  --margin-mobile: 15px;
  --margin-top-desk: 15px;

  // colors
  --main-color: #1c7f3c;
  --main-color-active: #10da50;
  --black: #000;
  --main-text-color: #434343;
  --second-text-color: #000;
  --third-text-color: #000;
  --white-text: #fff;
  --features-text: #5f5f5f;
  --grey-text: #d1cdcd;
  --cards-info-text: #434343;
  --red-bil: #750916;
  --box-bg: #fff;
  --item-before-bg: #fff;
  --tag-bg: #fff;
  --main-bg: #f4f4f4;
  --main-border: #e7e7e7;
  --button-bg: #1c7f3c;
  --arrow-bg: #707070;
  --green-tag-bg: #1c7f3c;
  --news-slide-card-bg: #104372;
  --news-slide-card-bg2: #3e455f;
  --footer-grey: #2f3234;

  // burger
  --burger-width: 30px;
  --burger-height: 20px;
  --burger-line-height: 2px;
}