.news-cards {
  padding-top: 80px;
  padding-bottom: var(--margin-big-desktop);
  background-color: var(--main-bg);

  &__news-text {
    margin: 0;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.news-card {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  padding: 14px 1px 14px 18px;
  max-width: 640px;
  background-color: var(--box-bg);

  &__about {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 20px;
    padding-left: 40px;
    max-width: 320px;
  }

  &__news-text {
    flex-grow: 1;
    margin: 0;
  }

  &__news-tag {
    margin-bottom: 20px;
  }

  &__news-date {
    margin-top: auto;
  }
}