.product-about {
  &__container {
    display: flex;
    justify-content: flex-start;
  }

  &__breadcrumbs {
    margin-bottom: 30px;
  }

  &__image {
    width: 40%;
  }

  &__content {
    align-self: flex-start;
  }

  &__header {
    padding-bottom: 20px;
  }

  &__title {
    margin: 0;
  }

  &__text {
    margin: 0;
    padding-bottom: 20px;
    padding-right: 120px;
  }

  &__article-number {
    padding: 16px 0 16px 30px;
    background-color: var(--main-bg);
  }
}