.testimonials {
  margin-bottom: var(--margin-big-desktop);
  background-image: url("../img/testimonials/privilege.png");
  background-size: cover;
  background-repeat: no-repeat;

  &__header {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    margin-bottom: 48px;
  }

  &__tag {
    color: var(--second-text-color);
  }

  &__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding-bottom: 50px;
    width: 100%;
    max-height: 400px;
  }

  &__about {
    margin: 0;
    color: var(--white-text);
  }

  &__item {
    position: relative;
    padding-bottom: 45px;
    padding-left: 60px;
    width: 42%;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 50%;
      width: 40px;
      height: 40px;
      background-image: url("../img/plus.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-color: var(--item-before-bg);
    }
  }
}
