/* stylelint-disable */
@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin large-desktop {
  @media (min-width: (1440px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin min-width-tablet {
  @media (min-width: (1024px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin mini-mobile {
  @media (max-width: (375px)) {
    @content;
  }
}
