.gallery {
  margin-top: 35px;
  margin-bottom: var(--margin-big-desktop);

  &__list {
    display: grid;
    grid-auto-flow: dense;
    grid-auto-rows: 220px;
    grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
    gap: 20px;
  }

  &__title {
    margin-bottom: 50px;
  }

  &__item {
    position: relative;
    background-color: var(--main-bg);

    &_lower {
      grid-row: span 1;
    }

    &_higher {
      grid-row: span 2;
    }
  }

  &__img {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
}