.features {
  display: flex;
  flex-direction: column;

  &__item {
    position: relative;
    border-radius: 1000px;
    padding: 21px 0 23px 84px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    color: var(--third-text-color);
    background-color: var(--box-bg);

    &::after {
      content: "";
      position: absolute;
      left: 5%;
      top: 30%;
      width: 30px;
      height: 30px;
      background-image: url("../img/featuresItem.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }

    &:not(:last-child) {
      margin-bottom: 25px;
    }

    &_width {
      padding-top: 30px;
      padding-bottom: 28px;

      &::after {
        left: 10%;
      }
    }

    &_short {
      padding-right: 140px;
    }
  }

  &__item span {
    font-weight: 700;
    color: var(--main-color);
  }
}