.characteristics {
  margin-bottom: var(--margin-big-desktop);

  &__information {
    display: flex;
    justify-content: space-between;
    margin-bottom: 75px;
  }

  &__title {
    max-width: 680px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 13px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    padding: 112px 40px 16px 40px;
    max-width: 315px;
    background-position: 40px 16px;
    background-repeat: no-repeat;
    background-color: var(--main-bg);

    &--sea-container {
      background-image: url("../img/characteristics/sea-container.svg");
    }

    &--oil {
      background-image: url("../img/characteristics/oil.svg");
    }

    &--electrics-lights {
      background-image: url("../img/characteristics/electrics-lights.svg");
    }

    &--gas-jet {
      background-image: url("../img/characteristics/gas-jet.svg");
    }

    &--color-touch {
      background-image: url("../img/characteristics/color-touch.svg");
    }

    &--pumps {
      background-image: url("../img/characteristics/pumps.svg");
    }

    &--storage {
      background-image: url("../img/characteristics/storage.svg");
    }

    &--automatics {
      background-image: url("../img/characteristics/automatics.svg");
    }
  }

  &__text {
    display: block;
    min-width: 255px;
    margin: 0;
    flex-grow: 0;
    margin-bottom: auto;
    font-weight: 400;
    font-size: 15px;
  }
}