.leaders {
  padding: 50px 45px 51px 60px;
  background-color: var(--main-bg);

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  &__item {
    max-width: 320px;

    .leaders__card {
      height: 100%;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    min-height: 157px;
  }

  &__post {
    margin: 0;
    padding-bottom: 6px;
    font-weight: 600;
    font-size: 15px;

    &_bottom {
      margin-top: auto;
    }
  }

  &__text {
    margin: 0;
    padding-bottom: 16px;
    font-weight: 400;
    font-size: 15px;
  }

  &__name {
    margin: 0;
    padding-bottom: 10px;
    font-weight: 600;
    font-size: 18px;
    color: var(--main-color);
  }

  &__phone {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    color: var(--second-text-color);
  }
}