.vlog {
  margin-bottom: var(--margin-big-desktop);

  &__container {
    display: flex;
    justify-content: space-between;
  }
}

.video {
  position: relative;
  display: inline-block;
  margin: 0px;
  width: 638px;
  height: 338px;
  background-color: aqua;

  &.hero__video {
    width: 770px;
    min-height: 430px;
  }
}

.video iframe,
.video img {
  display: block;
  border: none;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.video-play {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.video-play-shape {
  fill: #212121;
  fill-opacity: 80%;
}

.video-play-icon {
  fill: #fff;
}

.video-play:focus {
  outline: none;
}

.video:hover .video-play-shape,
.video:focus .video-play-shape {
  fill: #f00;
  fill-opacity: 100%;
}