.recycling-characteristics {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  &__item {
    position: relative;
    width: 50%;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 30px;
      height: 30px;
      background-image: url("../../img/svg/check-green.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }

  &__title {
    margin: 0;
    padding-top: 50px;
    padding-bottom: 10px;
    padding-right: 50px;
    font-weight: 700;
    font-size: 18px;
  }

  &__text {
    margin: 0;
    padding-right: 101px;
    padding-bottom: 18px;
    font-weight: 400;
    font-size: 15px;
  }
}

.type-recycling {
  margin-bottom: var(--margin-big-desktop);

  &__gallery {
    display: flex;
    width: 59%;
    padding: 40px 40px 0px 40px;
    background-color: var(--main-bg);
  }

  &__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  &__cards {
    width: 41%;
  }

  &__title {
    padding-right: 190px;
    padding-bottom: 70px;
  }
}

.recycling-gallery {
  display: flex;

  &__img {
    max-width: 312px;
  }

  &__text {
    text-align: right;
  }

  &__item {
    &:not(:last-child) {
      padding-right: 50px;
    }
  }
}