.instructions {
  margin-bottom: var(--margin-big-desktop);
  background-image: url("../img/background/instructions-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;

  &__container {
    display: flex;
    justify-content: space-between;
    padding: 49px 0 58px;
  }

  &__images {
    display: flex;

    &:not(:last-child) {
      padding-right: 30px;
    }
  }

  &__img {
    &:not(:last-child) {
      padding-right: 30px;
    }
  }

  &__about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 370px;
  }

  &__title {
    margin: 0;
    padding-right: 40px;
    color: var(--white-text);
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    color: var(--white-text);
  }
}

.instructions-quality-control {
  &__about {
    display: flex;
    justify-content: space-between;
    max-width: 100%;
  }

  &__title {
    margin: 0;
    padding-top: 10px;
    width: 49%;
    padding-right: 40px;
    color: var(--white-text);
  }

  &__text {
    width: 49%;
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    color: var(--white-text);

    span {
      font-weight: 700;
      font-size: 15px;
    }
  }
}