.montage-testimonials {
  margin-bottom: var(--margin-big-desktop);
  background-image: url("../img/testimonials/privilege.png");
  background-size: cover;
  background-repeat: no-repeat;

  &__container {
    display: flex;
    justify-content: space-between;
    padding: 47px 0;
  }

  &__schema {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 48px;
    width: 32%;
  }

  &__tag {
    color: var(--second-text-color);
  }

  &__title {
    margin: 0;
  }

  &__text {
    font-weight: 400;
    font-size: 13px;
    color: var(--white-text);
  }

  &__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding-bottom: 50px;
    width: 47%;
  }

  &__about {
    margin: 0;
    color: var(--white-text);
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 35px;
    padding-left: 60px;
    width: 82%;
    height: 36px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 50%;
      width: 40px;
      height: 40px;
      background-image: url("../img/plus.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-color: var(--item-before-bg);
      transform: translateY(-50%);
    }
  }
}