.steps {
  margin-bottom: var(--margin-big-desktop);

  &__container {
    padding: 56px 60px;
    background-color: var(--main-bg);
  }

  &__list {
    counter-reset: steps;
    display: flex;
    justify-content: space-between;
  }

  &__title {
    padding-top: 56px;
    padding-right: 100px;
    font-weight: 700;
    font-size: 15px;
    color: var(--main-color);

    &_long {
      padding-right: 10px;
    }
  }

  &__item {
    position: relative;
    width: 330px;

    &::before {
      counter-increment: steps;
      content: "0"counter(steps);
      position: absolute;
      padding-top: 4%;
      top: 10%;
      border-radius: 50%;
      width: 42px;
      height: 42px;
      font-weight: 700;
      font-size: 16px;
      text-align: center;
      color: var(--white-text);
      background-color: var(--main-color);
    }

    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        left: 60px;
        top: 38px;
        width: 350px;
        height: 8px;
        background-image: url("../img/arrow-steps.svg");
        background-size: cover;
        background-repeat: no-repeat;
      }
    }
  }
}