.montage-started {
  margin-bottom: var(--margin-big-desktop);

  &__information {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 35px;
  }

  &__title {
    max-width: 1000px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
    gap: 13px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    padding: 112px 40px 16px 40px;
    max-width: 315px;
    background-position: 40px 16px;
    background-repeat: no-repeat;
    background-color: var(--main-bg);

    &--technologies {
      display: flex;
      align-items: center;
      justify-content: center;
      grid-column: 3 / 5;
      padding: 0;
      max-width: 643px;

      .equipments__text {
        margin-bottom: 0;
      }
    }

    &--gear {
      background-image: url("../img/montage/gear.svg");
    }

    &--checked {
      background-image: url("../img/montage/checked.svg");
    }

    &--clock {
      background-image: url("../img/montage/clock.svg");
    }

    &--repair-man {
      background-image: url("../img/montage/repair-man.svg");
    }
  }

  &__text {
    display: block;
    flex-grow: 0;
    margin: 0;
    margin-bottom: auto;
    min-width: 255px;
    font-weight: 400;
    font-size: 15px;
  }
}