.viewer {
  margin-bottom: var(--margin-big-desktop);

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    padding-bottom: 21px;
  }

  &__title {
    width: 49%;
  }

  &__text {
    margin: 0;
    width: 49%;
    font-weight: 400;
    font-size: 15px;
    text-align: left;
  }

  &__gallery {
    display: flex;
    justify-content: space-between;
  }
}