.mobile-footer {
  display: none;

  &__office {
    display: flex;
    flex-direction: column;
    background-color: var(--black);
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    padding-top: 26px;
    padding-bottom: 35px;
  }

  &__phone-link {
    font-weight: 700;
    font-size: 18px;
    color: var(--white-text);
  }

  &__email-link {
    font-weight: 400;
    font-size: 14px;
    color: var(--white-text);
  }

  &__numbers-item {
    padding-bottom: 24px;
  }

  &__title {
    margin: 0;
    padding-bottom: 3px;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }

  &__numbers-phone {
    font-weight: 500;
    font-size: 16px;
    color: var(--white-text);
  }

  @include tablet {
    display: block;
  }
}