.breadcrumb {
  @include flex;

  z-index: 3;

  li {
    position: relative;
    margin: 0;
    padding: 0;

    &:not(:last-child)::after {
      content: "/";
      position: absolute;
      right: 2px;
      top: 2px;
      width: 8px;
      font-size: 15px;
      color: var(--white-text);
    }

    &:first-child {
      margin-left: 7.5%;

      @include for-desktop {
        margin-left: 0%;
      }

      @include large-desktop {
        margin-left: 0%;
      }

      @include tablet {
        margin-left: 0%;
      }
    }
  }

  &__link {
    display: inline;
    padding: 0 14px 0 0;
    font-family: var(--third-family);
    font-weight: 400;
    font-size: 12px;
    color: var(--white-text);
    opacity: 0.65;

    &_active {
      color: var(--white-text);
      opacity: 1;
      cursor: default;
    }

    &_black-text {
      color: var(--second-text-color);
    }

    &:hover {
      opacity: 1;
      transition: opacity 0.3s ease-in-out;
    }
  }
}

